<template>
  <div class="location">
    <strong>:</strong>
    <span> >> </span>
  </div>
  <div class="providerView">
    <p><strong></strong><span>{{user.userCode}}</span></p>
    <p><strong></strong><span>{{user.userName}}</span></p>
    <p><strong></strong><span>{{user.gender == 1 ? '' : ''}}</span></p>
    <p><strong></strong><span>{{birthday}}</span></p>
    <p><strong></strong><span>{{user.phone}}</span></p>
    <p><strong></strong><span>{{user.address}}</span></p>
    <p><strong></strong><span>{{user.roleName}}</span></p>
    <div class="providerAddBtn">
      <input type="button" name="back" value="" @click="back">
    </div>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: 'UserView',
  props: ['id'],
  data() {
    return {
      user: {}
    }
  },
  computed: {
    birthday() {
      let birthday = this.user.birthday || '';
      return birthday.substring(0, 10);
    }
  },
  methods: {
    back() {
      this.$router.back();
    }
  },
  created() {
    axios.get('http://localhost:8080/users/' + this.id).then(res => {
      if (res.status == 200) this.user = res.data;
    });
  },
}
</script>