<template>
  <div class="location">
    <strong>:</strong>
    <span> >> </span>
  </div>
  <div class="providerView">
    <p><strong></strong><span>{{provider.proCode}}</span></p>
    <p><strong></strong><span>{{provider.proName}}</span></p>
    <p><strong></strong><span>{{provider.proContact}}</span></p>
    <p><strong></strong><span>{{provider.proPhone}}</span></p>
    <p><strong></strong><span>{{provider.proFax}}</span></p>
    <p><strong></strong><span>{{provider.proDesc}}</span></p>
    <div class="providerAddBtn">
      <input type="button" name="back" value="" @click="back">
    </div>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: 'ProviderView',
  props: ['id'],
  data() {
    return {
      provider: {}
    }
  },
  methods: {
    back() {
      this.$router.back();
    }
  },
  created() {
    axios.get('http://localhost:8080/providers/' + this.id).then(res => {
      if (res.status == 200) this.provider = res.data;
    });
  },
}
</script>