<template>
  <div class="location">
    <strong>:</strong>
    <span> >> </span>
  </div>
  <div class="providerView">
    <p><strong></strong><span>{{bill.billCode}}</span></p>
    <p><strong></strong><span>{{bill.productName}}</span></p>
    <p><strong></strong><span>{{bill.productUnit}}</span></p>
    <p><strong></strong><span>{{bill.productCount}}</span></p>
    <p><strong></strong><span>{{bill.totalPrice}}</span></p>
    <p><strong></strong><span>{{bill.proName}}</span></p>
    <p><strong></strong>{{bill.isPayment == 1 ? '' : ''}}</p>
    <div class="providerAddBtn">
      <input type="button" name="back" value="" @click="back">
    </div>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: 'BillView',
  props: ['id'],
  data() {
    return {
      bill: {}
    }
  },
  methods: {
    back() {
      this.$router.back();
    }
  },
  created() {
    axios.get('http://localhost:8080/bills/' + this.id).then(res => {
      if (res.status == 200) this.bill = res.data;
    });
  },
}
</script>