<template>
  <div class="location">
    <strong>:</strong>
    <span></span>
  </div>
  <div class="providerAdd">
    <input type="hidden" name="method" value="savepwd">
    <div class="info">{{message}}</div>
    <div class="">
      <label for="oldPassword"></label>
      <input type="password" name="oldpassword" v-model="oldpassword" @focus="remind('* ', $event)" @blur="validate('oldpassword',  $event)">
      <span color="red">*</span>
    </div>
    <div>
      <label for="newPassword"></label>
      <input type="password" name="newpassword" v-model="newpassword" @focus="remind('* 620', $event)" @blur="validate('newpassword',  $event)">
      <span color="red">*</span>
    </div>
    <div>
      <label for="rnewpassword"></label>
      <input type="password" name="rnewpassword" v-model="rnewpassword" @focus="remind('* ', $event)" @blur="validate('rnewpassword',  $event)">
      <span color="red">*</span>
    </div>
    <div class="providerAddBtn">
      <input type="button" name="add" value="" @click="save" />
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import { showInfoTip } from '../../composables/utils';
export default {
  name: "ModifyPwd",
  data() {
    return {
      oldpassword: '',
      oldpasswordFit: false,
      newpassword: '',
      newpasswordFit: false,
      rnewpassword: '',
      rnewpasswordFit: false
    }
  },
  methods: {
    remind(info, $event) {
      showInfoTip($event, 'info', info);
    },
    
    validate(field, $event) {
      if (field == 'oldpassword') {
        if (this.oldpassword == '') {
          showInfoTip($event, 'error', '');
          this.oldpasswordFit = false;
          return;
        }

        let user = this.session.user;
        if (!user) {
          showInfoTip($event, 'error', '');
          this.oldpasswordFit = false;
          return;
        }

        if (user.userPassword != this.oldpassword) {
          showInfoTip($event, 'error', '');
          this.oldpasswordFit = false;
        } else {
          this.oldpasswordFit = true;
          showInfoTip($event, 'success', '');
        }
      }

      if (field == 'newpassword') {
        let isFit = (this.newpassword.length > 6 && this.newpassword.length < 20);
        if (isFit) showInfoTip($event, 'success', '');
        else showInfoTip($event, 'error', '');
        this.newpasswordFit = isFit;
      }

      if (field == 'rnewpassword') {
        let isFit = (this.rnewpassword.length > 6 && this.rnewpassword.length < 20 && this.rnewpassword == this.newpassword);
        if (isFit) showInfoTip($event, 'success', '');
        else showInfoTip($event, 'error', '');
        this.rnewpasswordFit = isFit;
      }
    },

    getIsAllFit(fits) {
      let isAllFit = true;
      fits.forEach((fit) => {
        if (!this[fit]) isAllFit = false;
      });
      if (!isAllFit) alert('');
      return isAllFit;
    },

    save() {
      let fits = ['oldpasswordFit', 'newpasswordFit', 'rnewpasswordFit'];
      let isAllFit = this.getIsAllFit(fits);
      if (!isAllFit) return;

      axios.put('http://localhost:8080/users', {
        id: this.session.user.id,
        userPassword: this.rnewpassword,
      }).then(res => {
        if (res.data) {
          $.notify("", { className: 'success', position: 'left top' });
          this.session.user.userPassword = this.rnewpassword;
        } else $.notify("", { className: 'error', position: 'left top' });
      });
    },
  }
}
</script>