<template>
  <header class="publicHeader">
    <h1><router-link to="/"></router-link></h1>
    <div class="publicHeaderR">
      <p><span style="color: #fff21b">{{userName}}</span> , </p>
      <router-link to="/login"></router-link>
    </div>
  </header>
  <section class="publicTime">
    <span id="time"></span>
    <a href="javascript:;">IE10+</a>
  </section>
</template>

<script>
export default {
  name: 'Header',
  data() {
    return {
      userName: ''
    }
  },
  created() {
    let user = this.session.user;
    if (user) this.userName = user.userName || '';
  }
}
</script>